.catalog-pagination {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  padding:0;
}

.catalog-pagination-button {
  @include reset-block;
  background: transparent;
  border: none;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;

  &:hover {
    color:  $brown;
}
&:focus{

  color:$brown;
}
&:active{
transform: scale(0.9);
transition: 0.2s;
}
}

.catalog-pagination-list {
  @include reset;
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
margin-left: -37px;
}
.catalog-pagination-item{
  &:not(:last-child){
    margin-right: 12px;
  }
}
.catalog-pagination-link {
  display: block;
  color:$grey;

  &--active{
    color:$black;
  }

  &:hover{
    color:$black;
    transform: scale(1.2);
    transition: 0.2s;
  }
  &:focus{
    transform: scale(0.9);
    transition: 0.2s;
  }
  &:active{
transform: scale(0.9);
transition: 0.2s;
  }
}

@media (max-width: $tablet-width) {
  .catalog-pagination {
    padding:35px calc(50% - 344px) 70px calc(50% - 346px);
  }
}
@media (max-width: $mobile-width) {
  .catalog-pagination{
  justify-content: flex-start;
  align-content: center;
  justify-content: center;
  padding:8px 0 63px 0;

  }
  .catalog-pagination-list{
    margin-left: 0;
    min-width:133px;
  }
  .catalog-pagination-button{
    display: none;
  }
  .catalog-pagination-item{
    &:not(:last-child){
      margin-right: 23px;
    }
  }
}
